.form {
	@include row; }

.form__label,
.form__button {
	flex-basis: 100%;
	max-width: 100%; }

.form__input {
	display: flex;
	width: 100%;
	height: 40px;
	padding: 5px 20px;
	font-weight: 400;
	font-size: 18px;
	line-height: 1.5;
	font-family: inherit;
	text-align: center;
	color: $base-color;
	background-color: #ffffff;
	@media (min-width: 420px) {
		text-align: left; } }

.form__label {
	@media (min-width: 420px) {
		flex-basis: 75%;
		max-width: 75%; } }

.form__button {
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 10px 20px;
	height: 40px;
	font-weight: 500;
	line-height: 14;
	line-height: 1.5;
	letter-spacing: 4px;
	text-transform: uppercase;
	color: #ffffff;
	background-color: #c6b393;
	cursor: pointer;
	transition: background-color .2s ease-in-out;
	&:focus,
	&:hover {
		background-color: rgba(#c6b393, 0.75); }
	&:active {
		background-color: rgba(#c6b393, 0.25); }
	@media (min-width: 420px) {
		flex-basis: 25%;
		max-width: 25%; } }
