.work {
	position: relative;
	margin-top: 100px;
	margin-bottom: 100px;
	@media (min-width: 992px) {
		margin-top: 140px;
		margin-bottom: 255px; } }

.work__header {
	@include container;
	@include row;
	margin-bottom: 55px;
	@media (min-width: 992px) {
		margin-bottom: 105px; } }

.work__footer {
	@include container;
	@include row;
	margin-top: 20px;
	margin-bottom: 20px; }

.worke__headline {
	position: relative;
	flex-basis: 100%;
	max-width: 100%;
	margin-bottom: 25px;
	@include col;
	font-weight: 500;
	font-size: 14px;
	line-height: 1.5;
	letter-spacing: 4px;
	text-transform: uppercase;
	color: rgba($base-color, 0.7);
	@media (min-width: 576px) {
		margin-bottom: 40px; } }

.worke__headline::before {
	display: inline-block;
	padding-right: 10px;
	content: "—"; }

.work__lead {
	margin-bottom: 35px;
	flex-basis: 100%;
	max-width: 100%;
	@include col;
	@include title;
	letter-spacing: -1px;
	@media (min-width: 992px) {
		flex-basis: 50%;
		max-width: 50%;
		margin-bottom: 0; } }

.work__footer-text {
	flex-basis: 100%;
	max-width: 100%;
	@include col;
	@media (min-width: 992px) {
		flex-basis: 66.66666667%;
		max-width: 66.66666667%; } }


.work__button {
	margin: 15px auto;
	@media (min-width: 992px) {
		margin: 0 15px 0 auto; } }

.work__controls {
	flex-basis: 100%;
	max-width: 100%;
	@media (max-width: 991.98px) {
		@include row; }
	@media (min-width: 992px) {
		display: flex;
		flex-basis: 45%;
		max-width: 45%;
		margin-left: 5%; } }

.work__control {
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 10px 15px;
	font-weight: 500;
	font-size: 14px;
	line-height: 1.5;
	text-decoration: none;
	text-transform: uppercase;
	letter-spacing: 4px;
	color: rgba($base-color, 0.3);
	transition: color .15s ease-in-out;
	&:not(.work__control_current):focus,
	&:not(.work__control_current):hover {
		color: $base-color; }
	&:not(.work__control_current):active {
		color: rgba($base-color, 0.15); } }

.work__control_current {
	color: $button;
	cursor: default; }

.work__items {
	@include container;
	@include row; }

.work__item {
	flex-basis: 100%;
	max-width: 100%;
	margin-bottom: 30px;
	@include col;
	@media (min-width: 576px) {
		flex-basis: 50%;
		max-width: 50%; }
	@media (min-width: 768px) {
		flex-basis: 33.33333333%;
		max-width: 33.33333333%; } }

.work__modal {
	display: block;
	width: 100%;
	height: 450px;
	transition: box-shadow .85s, transform 1.05s; }

.work__modal:focus-within,
.work__modal:hover {
	box-shadow: -30px 80px 200px rgba(0, 0, 0, 0.25);
	transform: translateY(-20px); }
