.client__headline { flex-basis: 100%; max-width: 100%; padding-left: 15px; padding-right: 15px; display: block; font-weight: 600; font-size: 22px; line-height: 1.25; font-family: inherit; letter-spacing: -1px; letter-spacing: -1px; }

@media (min-width: 576px) { .client__headline { font-size: 40px; } }

@media (min-width: 992px) { .client__headline { flex-basis: 58.33333333%; max-width: 58.33333333%; } }

.client__desc { display: none; padding-left: 15px; padding-right: 15px; }

@media (min-width: 992px) { .client__desc { display: block; flex-basis: 41.66666667%; max-width: 41.66666667%; } }

.client__lead { margin-top: 10px; margin-bottom: 15px; position: relative; font-weight: 500; font-size: 14px; line-height: 1.5; letter-spacing: 4px; text-transform: uppercase; color: rgba(34, 34, 34, 0.7); }

.client__lead::before { display: inline-block; padding-right: 10px; content: "—"; }

.client__list { margin: 0 auto; max-width: 1140px; display: flex; flex: 0 1 auto; flex-direction: row; flex-wrap: wrap; margin: 70px auto; justify-content: space-between; align-items: baseline; }

.client__item { padding-left: 15px; padding-right: 15px; }

@media (max-width: 767.98px) { .client__item { flex-basis: 50%; max-width: 50%; display: flex; padding: 25px 15px; justify-content: center; } }

.client__image { overflow: hidden; }

.footer { position: relative; color: #ffffff; background-color: #515151; }

.footer__wrap { margin: 0 auto; max-width: 1140px; display: flex; flex: 0 1 auto; flex-direction: row; flex-wrap: wrap; }

.footer__top, .footer__bottom { flex-basis: 100%; max-width: 100%; display: flex; flex: 0 1 auto; flex-direction: row; flex-wrap: wrap; align-items: center; }

.footer__top { padding-top: 50px; padding-bottom: 40px; }

@media (min-width: 576px) { .footer__top { padding-top: 60px; } }

.footer__bottom { position: relative; padding-top: 30px; padding-bottom: 25px; }

.footer__bottom::before { position: absolute; top: 0; right: 0; left: 0; display: block; margin: 0 15px; height: 1px; content: ""; background-color: rgba(255, 255, 255, 0.2); }

@media (min-width: 576px) { .footer__bottom { padding-bottom: 40px; } }

.footer__logo { display: none; }

@media (min-width: 992px) { .footer__logo { flex-basis: 16.66666667%; max-width: 16.66666667%; display: block; padding-left: 15px; padding-right: 15px; color: #ffffff; } }

.footer__logo svg { margin-top: 5px; height: 24px; color: #ffffff; }

.footer__links { display: none; }

@media (min-width: 576px) { .footer__links { flex-basis: 100%; max-width: 100%; width: 100%; display: flex; flex: 0 1 auto; flex-direction: row; flex-wrap: wrap; } }

@media (min-width: 576px) { .footer__links_top { margin: 5px 0 40px; } }

@media (min-width: 768px) { .footer__links_top { flex-basis: 65%; max-width: 65%; margin: 0; } }

@media (min-width: 992px) { .footer__links_top { flex-basis: 50%; max-width: 50%; } }

@media (min-width: 576px) { .footer__links_bottom { flex-basis: 65%; max-width: 65%; } }

@media (min-width: 992px) { .footer__links_bottom { flex-basis: 50%; max-width: 50%; } }

.footer__socials { flex-basis: 100%; max-width: 100%; width: 100%; display: flex; justify-content: space-around; }

@media (min-width: 576px) { .footer__socials { flex-basis: 35%; max-width: 35%; } }

@media (min-width: 768px) { .footer__socials { margin-left: auto; } }

.footer__form { padding-left: 15px; padding-right: 15px; flex-basis: 100%; max-width: 100%; width: 100%; }

@media (min-width: 768px) { .footer__form { flex-basis: 35%; max-width: 35%; } }

@media (min-width: 992px) { .footer__form { flex-basis: 33.33333333%; max-width: 33.33333333%; } }

.footer__link { display: flex; align-items: center; justify-content: center; padding-left: 15px; padding-right: 15px; font-size: 18px; line-height: 1.5; color: #ffffff; text-decoration: none; transition: color .2s ease-in-out; }

.footer__link:focus, .footer__link:hover { color: rgba(255, 255, 255, 0.75); }

.footer__link:active { color: rgba(255, 255, 255, 0.15); }

.footer__link_pale { color: rgba(255, 255, 255, 0.4); }

.footer__link_pale:focus, .footer__link_pale:hover { color: #ffffff; }

.footer__link_pale:active { color: rgba(255, 255, 255, 0.15); }

.footer__link-icon svg { width: auto; height: 24px; color: inherit; }

.form { display: flex; flex: 0 1 auto; flex-direction: row; flex-wrap: wrap; }

.form__label, .form__button { flex-basis: 100%; max-width: 100%; }

.form__input { display: flex; width: 100%; height: 40px; padding: 5px 20px; font-weight: 400; font-size: 18px; line-height: 1.5; font-family: inherit; text-align: center; color: #222222; background-color: #ffffff; }

@media (min-width: 420px) { .form__input { text-align: left; } }

@media (min-width: 420px) { .form__label { flex-basis: 75%; max-width: 75%; } }

.form__button { display: flex; align-items: center; justify-content: center; padding: 10px 20px; height: 40px; font-weight: 500; line-height: 14; line-height: 1.5; letter-spacing: 4px; text-transform: uppercase; color: #ffffff; background-color: #c6b393; cursor: pointer; transition: background-color .2s ease-in-out; }

.form__button:focus, .form__button:hover { background-color: rgba(198, 179, 147, 0.75); }

.form__button:active { background-color: rgba(198, 179, 147, 0.25); }

@media (min-width: 420px) { .form__button { flex-basis: 25%; max-width: 25%; } }

.menu { position: relative; }

.menu__wrap { position: fixed; top: 0; right: 0; z-index: 9996; display: block; width: 100%; height: 100vh; overflow: hidden; pointer-events: none; }

.menu__bg { position: fixed; top: -142vmax; right: -142vmax; z-index: 1; overflow: hidden; display: block; width: 284vmax; height: 284vmax; background-color: #f4f4f4; border-radius: 50%; transform: scale(0); transform-origin: 50% 50%; transition: transform 0.5s cubic-bezier(0.755, 0.05, 0.855, 0.06); }

.menu__inner { position: relative; z-index: 100; margin: 0 auto; max-width: 1140px; visibility: hidden; opacity: 0; transition: all 0.6s cubic-bezier(0.19, 0.88, 0.22, 1) 0.4s; }

.menu__nav { display: flex; height: 100%; justify-content: flex-start; align-items: center; }

.menu__header { display: flex; justify-content: flex-end; ppadding-top: 15px; padding-bottom: 15px; }

@media (min-width: 576px) { .menu__header { padding-top: 20px; padding-bottom: 20px; align-items: center; } }

@media (min-width: 992px) { .menu__header { padding-top: 45px; padding-bottom: 40px; } }

.menu__list { min-width: 320px; max-width: 50%; display: flex; flex: 0 1 auto; flex-direction: row; flex-wrap: wrap; list-style: none; }

.menu__item { flex-basis: 100%; max-width: 100%; }

.menu__link { display: inline-flex; padding: 10px 15px; font-weight: 600; display: block; font-weight: 600; font-size: 22px; line-height: 1.25; font-family: inherit; letter-spacing: -1px; color: #222222; letter-spacing: -1px; text-decoration: none; transform: translateZ(0); transition: color .2s ease-in-out; }

@media (min-width: 576px) { .menu__link { font-size: 40px; } }

.menu__link:hover, .menu__link:focus { color: rgba(34, 34, 34, 0.55); }

.menu__link:active { color: rgba(34, 34, 34, 0.15); }

.menu.is-active .menu__wrap { overflow: auto; pointer-events: all; }

.menu.is-active .menu__bg { transform: scale(1); }

.menu.is-active .menu__inner { visibility: visible; opacity: 1; }

.menu.is-active .menu__link { animation: slide-in .4s .2s backwards; }

.menu.is-active .menu__item:nth-of-type(2) .menu__link { animation-delay: .3s; }

.menu.is-active .menu__item:nth-of-type(3) .menu__link { animation-delay: .4s; }

.menu.is-active .menu__item:nth-of-type(4) .menu__link { animation-delay: .5s; }

.menu.is-active .menu__item:nth-of-type(5) .menu__link { animation-delay: .6s; }

@keyframes slide-in { 0% { opacity: 0;
    transform: translateX(80px); }
  100% { opacity: 1;
    transform: translateX(0); } }

.menu__button { display: inline-flex; margin: 0; padding: 15px; border: 0; overflow: visible; background-color: transparent; cursor: pointer; transition-property: opacity, filter; transition-duration: 0.15s; transition-timing-function: linear; }

.menu__button-box { position: relative; display: block; width: 34px; height: 25px; }

.menu__button-inner { top: 50%; margin-top: -1.5px; }

.menu__button-inner, .menu__button-inner::before, .menu__button-inner::after { position: absolute; display: block; width: 34px; height: 3px; background-color: #333333; transition-property: transform; transition-duration: 0.075s; transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19); }

.menu__button-inner::before, .menu__button-inner::after { content: ""; }

.menu__button-inner::before { top: -7px; transition: top 0.075s 0.12s ease, opacity 0.075s ease; }

.menu__button-inner::after { bottom: -7px; transition: bottom 0.075s 0.12s ease, transform 0.075s cubic-bezier(0.55, 0.055, 0.675, 0.19); }

.menu.is-active .menu__button-inner { transform: rotate(45deg); transition-delay: 0.12s; transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }

.menu.is-active .menu__button-inner::before { top: 0; opacity: 0; transition: top 0.075s ease, opacity 0.075s 0.12s ease; }

.menu.is-active .menu__button-inner::after { bottom: 0; transform: rotate(-90deg); transition: bottom 0.075s ease, transform 0.075s 0.12s cubic-bezier(0.215, 0.61, 0.355, 1); }

.search { position: relative; }

.search__wrap { position: relative; display: flex; }

.search__label { position: relative; width: 0; max-width: 320px; overflow: hidden; }

.search__label-text { position: absolute; top: 0; left: 0; display: block; padding: 5px 15px; font-weight: 400; font-size: 18px; line-height: 1.7; white-space: nowrap; transition: all 200ms; }

.search__input { display: inline-flex; padding: 7.5px 15px; font-weight: 400; font-size: 18px; line-height: 1.6; color: #222222; background-color: transparent; border-bottom: 2px solid rgba(34, 34, 34, 0.3); }

.search__button { display: inline-flex; width: 45px; height: 45px; padding: 5x 15px; align-items: center; justify-content: center; background-color: transparent; border: 2px solid transparent; }

.search__button:hover { border-color: rgba(34, 34, 34, 0.3); }

.search__icon svg { width: auto; height: 20px; }

.search:focus-within .search__label { width: auto; overflow: unset; }

.search:focus-within .search__label-text { opacity: 1; font-size: 10px; transform: translate(0, -50%); }
