.modal {
	position: relative; }

.modal__box {
	position: relative;
	display: block;
	width: 100%;
	height: 100%;
	overflow: hidden;
	background-color: darken($button, 15%); }

.modal__image {
	position: relative;
	z-index: 5;
	display: block;
	width: 100%;
	height: 100%; }

.modal__image img {
	display: block;
	width: 100%;
	height: 100%;
	line-height: 1;
	object-fit: cover;
	transition: transform 1.15s, opacity 1.15s; }

.modal__overlay {
	position: absolute;
	top: 0;
	right: 0;
	left: 0;
	bottom: 0;
	z-index: 10;
	opacity: 0;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: transparent;
	transition: all .75s; }

.modal__icon svg {
	display: block;
	width: auto;
	height: 65px; }

.modal__more {
	position: absolute;
	top: 0;
	right: 0;
	left: 0;
	bottom: 0;
	z-index: 15; }

.modal__box:focus-within .modal__image img,
.modal__box:hover .modal__image img, {
	transform: scale(1.1);
	opacity: .8; }

.modal__box:focus-within .modal__overlay,
.modal__box:hover .modal__overlay,
.modal_current .modal__overlay {
	opacity: 1;
	background-color: rgba($base-color, 0.15); }

